import { randomID } from "../utilities/index";

export default class Group {
  constructor(a) {
    this.owner = a.owner;
    this.ownerId = a.ownerId;
    this.groupId = randomID();
    this.createdTime = Date.now();
    this.groupName = a.groupName;
    this.img = a.img || "";
    this.membersId = [a.ownerId];
    this.inviteCode = randomID();
    this.lastUserMessage = "";
  }
}



