/* eslint-disable */

import { UPLOAD_IMAGE } from './actions.type';
import {} from './mutations.type';
import firebase from 'firebase';
import db, { storageRef } from "../database/firestore";


import query from '../database/db.actions';

const state = {};
const getters = {};
const mutations = {};
const actions = {
  [UPLOAD_IMAGE](c, data) {
    const uploadTask = storageRef.child(data.path).put(data.file);
    uploadTask.on(
      'state_changed',
      (snapshot) => {
        let progress = (snapshot.bytesTransferred / snapshot.totalBytes) * 100;
        console.log('Upload is ' + progress + '% done');
        switch (snapshot.state) {
          case firebase.storage.TaskState.PAUSED: // or 'paused'
            console.log('Upload is paused');
            break;
          case firebase.storage.TaskState.RUNNING: // or 'running'
            console.log('Upload is running');
            break;
        }
      },
      (error) => {
        console.log(error);
      },
      () => {
        uploadTask.snapshot.ref.getDownloadURL().then((downloadURL) => {
					console.log("File available at", downloadURL);
					query.saveProfileImage(db, data.userId, downloadURL, data.type);
				});
      },
    );
  },
};

const plugins = {};

export default {
  state,
  getters,
  mutations,
  actions,
  plugins,
};



