<template>
  <div>
    <nav
      class="navbar navbar-expand-md navbar-dark bg-dark fixed-top"
      :class="{' offset-2': !isMobile}"
    >
      <i class="fas fa-chevron-left fa-lg text-white mr-3" @click="openMenu()" v-if="isMobile"></i>
      <a
        class="navbar-brand text-white"
        style=" text-transform: capitalize;"
      >{{current || $route.name}}</a>
      
      <ul class="nav ml-auto" v-if="nav == 'group' && hasId">
        >
        <li class="nav-item">
          <a class="nav-link" @click="getInviteCode()">
            <i class="fas fa-user-plus text-white"></i>
          </a>
        </li>
        <li class="nav-item">
          <a class="nav-link" @click="groupSetup()">
            <i class="fas fa-cog text-white"></i>
          </a>
        </li>
        <li class="nav-item" v-if="isMobile">
          <a class="nav-link" @click="openUserList()">
            <i class="fas fa-user-friends text-white"></i>
          </a>
        </li>
      </ul>

      <ul class="nav ml-auto">
        <li class="nav-item" v-if="nav == 'personal' && hasId">
          <a class="nav-link" @click="leaveConversation()">
            <i class="fas fa-sign-out-alt text-white"></i>
          </a>
        </li>
        <li class="nav-item">
          <a class="nav-link">
            <i class="fas fa-cog text-white"></i>
          </a>
        </li>
      </ul>
      <!-- <button
        class="navbar-toggler"
        type="button"
        data-toggle="collapse"
        data-target="#navbarTogglerDemo02"
        aria-controls="navbarTogglerDemo02"
        aria-expanded="false"
        aria-label="Toggle navigation"
      >
        <span class="navbar-toggler-icon"></span>
      </button>

      <div class="collapse navbar-collapse" id="navbarTogglerDemo02">
        <form class="form-inline my-2 my-md-0">
          <ul class="navbar-nav mr-auto mt-2 mt-md-0">
            <li class="nav-item active" v-if="nav == 'group' && hasId">
              <a class="nav-link" @click="getInviteCode()">
                <i class="fas fa-user-plus"></i>
              </a>
            </li>
            <li class="nav-item active" v-if="nav == 'group' && hasId">
               <a class="nav-link" @click="groupSetup()">
                <i class="fas fa-cog text-white"></i>
              </a>
            </li>
          </ul>
          <input class="form-control mr-sm-2" type="search" placeholder="Search">
          <button class="btn btn-outline-success my-2 my-sm-0" type="submit">Search</button>
        </form> 
      </div>-->
    </nav>
  </div>
</template>

<script>
import { mapState } from "vuex";
import GroupSetup from "./GroupSetup";
import { convertDate, isMobile } from "../utilities";

export default {
  name: "Nav",
  data() {
    return {
      current: "",
      nav: this.$route.name,
      hasId: this.$route.params.id,
      group: {},
      isMobile: isMobile.any() ? true : false
    };
  },
  computed: {
    ...mapState({
      userInfo: state => state.auth.userInfo,
      getUserStatus: state => state.auth.userInfo.status,
      getUsername: state => state.auth.userInfo.name,
      notify: state => (state.auth.notify ? state.auth.notify : []),
      inactiveNotify: state =>
        state.auth.inactiveNoti ? state.auth.inactiveNoti : [],
      chatList: state => state.chat.chatList,
      pendingRequests: state => state.auth.pendingRequests,
      friendNotify: state => state.auth.friendNotify,
      converList: state => state.chat.converList,
      joinedGroupList: state => state.group.joinedGroupList
    })
  },
  watch: {
    $route(n) {
      this.current = '';
      this.nav = n.name;
      this.hasId = n.params.id;
      if (n.name == "personal") {
        try {
          this.group =
            this.chatList.filter(e => e.groupId == n.params.id)[0] || {};
          this.current =
            this.group.membersName[0] != this.userInfo.name
              ? this.group.membersName[0]
              : this.group.membersName[1];
        } catch (e) {}
      }
      if (n.name == "group") {
        this.hasId = n.params.id;
        this.group =
          this.joinedGroupList.filter(e => e.groupId == n.params.id)[0] || {};
        this.current = this.group.groupName;
        console.log(this.joinedGroupList);
      }
    }
  },
  methods: {
    getInviteCode: function() {
      if (this.nav == "group") {
        let url = this.group.inviteCode;
        console.log(url);
        this.$modal.show("dialog", {
          title: "Invite friend",
          text: `<div class="input-group">
									<input type="text" value="${url}" id="sharedLink" class="form-control" readonly>
									<div class="input-group-append">
										<button class="btn btn-outline-secondary" onclick="(function(){
											document.getElementById('sharedLink').select();
											document.getElementById('alert').classList.remove('text-hide');
											document.execCommand('copy');
										})(); return false;"><i class="fas fa-copy"></i></button>
									</div>
								</div>
								<div class="alert alert-success my-1 p-1 text-center text-hide" id="alert">
									Link has been copied to clipboard
                </div>`,
          buttons: [
            {
              title: "CLOSE"
            }
          ]
        });
      }
    },
    groupSetup: function() {
      console.log(this.group, this.userInfo);
      this.$modal.show(
        GroupSetup,
        { group: this.group, userInfo: this.userInfo },
        {
          height: "auto",
          width: this.isMobile ? "100%" : "50%",
          style: { "z-index": 9000 },
          scrollable: true
        },
        {
          closed: this.hide
        }
      );
    },
    hide() {
      this.$modal.hide("view-post");
      history.pushState(null, "", "/");
    },
    openMenu: function() {
      if (document.getElementById("listChat").style.left == "-100%") {
        document.getElementById("menu-overlay").classList.add("menu-overlay");
        document.getElementById("listChat").style.left = "0";
      } else {
        document
          .getElementById("menu-overlay")
          .classList.remove("menu-overlay");
        document.getElementById("listChat").style.left = "-100%";
      }
    },
    openUserList: function() {
      if (document.getElementById("userList").style.right == "-100%") {
        // document.getElementById("menu-overlay").classList.add('menu-overlay');
        document.getElementById("userList").style.right = "0";
      } else {
        // document.getElementById("menu-overlay").classList.remove('menu-overlay');
        document.getElementById("userList").style.right = "-100%";
      }
    },
    leaveConversation: function() {
      this.$modal.show("dialog", {
        title: "Leave Conversation",
        text: "Do you want to leave this conversation ?",
        buttons: [
          {
            title: "<button class='btn btn-warning'>CONFIRM</button>",
            handler: () => {
              this.$store.dispatch('leaveConversation', {
                uid: this.userInfo.id,
                groupId: this.group.groupId
              }).then(() => {
                this.$modal.hide('dialog')
                this.$router.push('/personal');
                this.current = this.$route.name;
              });
            }
          },
          {
            title: "CANCEL"
          }
        ]
      });
    },
    
  }
};
</script>

<style>
.nav-link {
  cursor: pointer;
}
.vue-dialog-button .btn {
  height: 100%;
  width: 100%;
}
</style>



