<template>
  <div
    class="p-0"
    style="top: 56px;width: 100%;"
    :style="isMobile ? 'position: absolute' : ''"
    :class="[{'chat-content' : isPostView == 'public'}, {'col-10 bg-light ': !isMobile}]"
    v-on:scroll="scrollA"
  >
    <Nav/>
    <router-view></router-view>
    <!-- <Message/> -->
  </div>
</template>

<script>
import { mapGetters, mapState } from "vuex";
import Message from "./Message";
import Nav from "./Nav";
import { isMobile } from "../utilities";

export default {
  name: "ChatMessagePanel",
  computed: mapState({
    post: state => state.postView.postList,
    userInfo: state => state.auth.userInfo,
    unsub: state => state.postView.unsub,
    limit: state => state.postView.limit
  }),
  components: { Message, Nav },
  data() {
    return {
      isPostView: this.$route.name,
      lastFetch: 0,
      isMobile: isMobile.any()
    };
  },
  watch: {
    $route() {
      this.isPostView = this.$route.name;
    }
  },
  mounted() {
    // window.addEventListener("scroll", this.scrollA);
    // window.addEventListener("unload", this.removeUser);
  },
  methods: {
    scrollA(e) {
      let now = Date.now();
      let d = this.post[this.post.length - 1].createdDate;
      // let top = document.scrollingElement.scrollTop;
      // let wheight = window.innerHeight;
      // let height = document.scrollingElement.scrollHeight;
      if (
        Math.ceil(e.target.scrollTop + e.target.offsetHeight) ==
          e.target.scrollHeight &&
        now - this.lastFetch > 500
      ) {
        // this.userInfo.email
        // 	? (document.documentElement.scrollTop = 375)
        // 	: (document.documentElement.scrollTop = 0);
        this.lastFetch = now;
        this.unsub();
        this.$store.dispatch("FETCH_POST_LIST", {
          date: d,
          operator: "<",
          limit: +this.limit + 10
        });
        this.$forceUpdate();
      }
    }
  }
};
</script>

<style>
.home {
  height: 100vh;
}
.main-message-board {
  height: 10%;
}
.chat-content {
  height: calc(100% - 56px);
  overflow-y: scroll;
}
body {
  height: 100vh;
  overflow: hidden;
}
.card-body.container-fluid {
  height: calc(100vh - 67px);
}

.main-message {
  height: 100%;
  /* overflow-y: scroll; */
}

* {
  border-radius: 0 !important;
}
</style>



