<template>
  <div id="app">
    <Home/>
    <modal name="view-post"></modal>
    <modals-container/>
    <v-dialog/>
  </div>
</template>

<script>
import Home from "./components/Home";
import { swipedetect } from "./utilities";

export default {
  name: "App",
  components: {
    Home
  },
  methods: {
    setCaret() {
      // setTimeout(() => {
      var el = document.getElementById("comment");
      var range = document.createRange();
      var sel = window.getSelection();
      if (!el || el.innerHTML == "") {
        el.focus();
      } else {
        range.setStart(
          el.childNodes[el.childNodes.length - 1],
          el.childNodes[el.childNodes.length - 1].length
        );
        range.collapse(true);
        sel.removeAllRanges();
        sel.addRange(range);
        el.focus();
      }
      // }, 1);
    }
  },
  mounted() {
    swipedetect(document.body, dir => {
      if (dir == "right") {
        if (document.getElementById("listChat").style.left != "0px") {
          document.getElementById("listChat").style.left = "0";
        }
      } 
    });
    window.addEventListener("keypress", () => {
      if (document.getElementById("comment")) this.setCaret();
    });
  }
};
</script>

<style>
@import url("https://fonts.googleapis.com/css?family=Montserrat:400,600,700|Work+Sans:300,400,700,900");
*:not(i){
  outline-width: 0;
  font-family: "Montserrat" !important;
}
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #566777;
}
.list-group-item-action {
  cursor: pointer;
}
</style>



