<template>
  <div id="groupSetup">
    <div class="card">
      <div class="card-header">
        GROUP OVERVIEW
      </div>
      <div class="card-body">
        <div class="row">
          <div class="col-6 ">
              <div class="rounded-circle border mx-auto" style="height: 100px; width: 100px"></div>
              <button class="btn btn-outline-secondary d-flex mx-auto my-2">Upload image</button>
          </div>
          <div class="col-6">
            <div class="form-group">
              <label for="groupName" class="bmd-label-floating">GROUP NAME</label>
              <input type="text" class="form-control" id="groupName" v-model="group.groupName" @keyup="setTimer()"
            @keydown="clearTimer()">
              <div class="small bg-success">{{changeGroupNameAlert}}</div>
            </div>
            <div class="input-group mb-3">
              <label for="inviteCode" class="bmd-label-floating">INVITE CODE</label>
              <input type="text" class="form-control px-2" id="inviteCode" style="cursor: pointer" readonly v-model="group.inviteCode">
              <div class="input-group-append">
                <button class="btn btn-outline-secondary" type="button" @click="copyInviteCode()">{{inviteCodeTooltip}}</button>
              </div>
            </div>          
          </div>
        </div>
          <hr>
        <div class="row">
          <button class="btn btn-danger mx-auto" v-if="userInfo._id == group.ownerId" @click="deleteGroup()">DELETE GROUP</button>

        </div>

      </div>
      <div class="card-footer"></div>
    </div>
  </div>
</template>

<script>
import db from "../database/firestore";
import query from "../database/db.actions";

export default {
  name: "GroupSetup",
  props: {
    group: Object,
    userInfo: Object
  },
  data() {
    return {
      inviteCodeTooltip: 'copy',
      changeGroupNameAlert: '',
      typingInterval: 1000,
      typingTimer: null
    }
  },
  methods: {
    setTimer: function() {
      clearTimeout(this.typingTimer);
      this.typingTimer = setTimeout(this.changeGroupName, this.typingInterval);
    },
    clearTimer: function() {
      clearTimeout(this.typingTimer);
    },
    copyInviteCode: function() {
      document.getElementById('inviteCode').select();
      document.execCommand("copy");
      this.inviteCodeTooltip = "Copied";
      setTimeout(() => {
        this.inviteCodeTooltip = 'copy';
      }, 5000);
      $('[data-toggle="tooltip"]').tooltip()
    },
    changeGroupName: function() {
      let groupId = this.group.groupId,
          groupName = this.group.groupName;
      db.collection('groups').where("groupId", "==", groupId).get().then(doc => {
        console.log(doc)
        if (doc.empty) return;
        let id = doc.docs[0].id;
        db.collection('groups').doc(id).set({groupName}, {merge: true}).then(d => {
          console.log(d)
          this.changeGroupNameAlert = "Group name changed"
        })
      })
    }
  },
  mounted() {
    $('[data-toggle="tooltip"]').tooltip()
    $("#groupSetup").bootstrapMaterialDesign();
    console.log(this.group)
  }
};
</script>

<style>
</style>



