<template>
  <!-- <div v-on:scroll="scrollA"> 
    <Navbar />
    <div class="router-view mt-5">
      <router-view></router-view>
    </div>
		<div v-show="false">
			<Login />
		</div>
    <Footer />
  </div>-->
  <div v-if="this.userInfo.name" class="container-fluid">
    <div class="row home">
      <div id="menu-overlay" style="transition: all 0.5s ease" @click="hide()"></div>
      <Menu/>
      <ChatMessagePanel/>
    </div>
  </div>
  <Login v-else/>
</template>

<script>
import Navbar from "../views/Navbar";
import Footer from "../views/Footer";
import Login from "../components/LoginScreen";
import Menu from "./Menu";
import ChatMessagePanel from "./ChatMessagePanel";

import {
  LOGIN_WITH_G,
  FETCH_POST_LIST,
  REMOVE_USER,
  GET_USER_LIST
} from "../store/actions.type";

import { mapGetters, mapState } from "vuex";

export default {
  name: "Home",
  components: {
    Navbar,
    Footer,
    Login,
    Menu,
    ChatMessagePanel
  },
  data() {
    return {
      lastFetch: 0,
      isHome: this.$route.path
    };
  },
  computed: mapState({
    post: state => state.postView.postList,
    userInfo: state => state.auth.userInfo,
    unsub: state => state.postView.unsub,
    limit: state => state.postView.limit
  }),
  mounted() {
    this.$store.dispatch(LOGIN_WITH_G);
    // this.$store.dispatch(GET_USER_LIST, this.userInfo);
  },
  watch: {
    $route() {
      if (this.$route.path != "/")
        window.removeEventListener("scroll", this.scrollA);
    }
  },
  methods: {
    scrollA(e) {
      let now = Date.now();
      let d = this.post[this.post.length - 1].createdDate;
      // let top = document.scrollingElement.scrollTop;
      // let wheight = window.innerHeight;
      // let height = document.scrollingElement.scrollHeight;
      if (
        Math.ceil(document.documentElement.scrollTop + window.innerHeight) ==
          document.documentElement.offsetHeight &&
        now - this.lastFetch > 500
      ) {
        // console.log(this.post);
        // this.userInfo.email
        // 	? (document.documentElement.scrollTop = 375)
        // 	: (document.documentElement.scrollTop = 0);
        this.lastFetch = now;
        this.unsub();
        this.$store.dispatch(FETCH_POST_LIST, {
          date: d,
          operator: "<",
          limit: +this.limit + 10
        });
        this.$forceUpdate();
      }
    },
    removeUser(e) {
      this.$store.dispatch(REMOVE_USER, this.userInfo);
    },
    hide() {
      document.getElementById("listChat").style.left = "-100%";
      document.getElementById("menu-overlay").classList.remove('menu-overlay');
    }
  },
  mounted() {
    window.addEventListener("scroll", this.scrollA);
    window.addEventListener("unload", this.removeUser);
    console.log(this.userInfo)
  },
  destroyed() {}
};
</script>

<style>
.full-size {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
}
.menu-overlay {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.5);
  z-index: 9998;
}

*::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.1);
  background-color: #f5f5f5;
}

*::-webkit-scrollbar {
  width: 6px;
  height: 6px;
  background-color: #f9f9f9;
}

*::-webkit-scrollbar-thumb {
  background-color: #c8c8c8;
  border-radius: 10px;
}

html {
  scroll-behavior: smooth;
}
.home {
  height: 100vh;
}
.main-message-board {
  height: 10%;
}

body {
  height: 100vh;
  overflow: hidden;
}
.card-body.container-fluid {
  height: calc(100vh - 87px - 56px);
}

.main-message {
  height: 100%;
  overflow-y: scroll;
}

* {
  border-radius: 0 !important;
}
.card {
  box-sizing: content-box;
}
</style>



