/* eslint-disable */

import db from '../database/firestore';
import query from '../database/db.actions';
import groupModel from '../models/group.model';

let state = {
  newGroupId: '',
  createGroupStatus: '',
  joinedGroupList: [],
  groupUserList: []
};
let getters = {};
let mutations = {
  CREATE_GROUP_SUCCESS(state, data) {
    state.newGroupId = data.groupId;
    state.createGroupStatus = "success";
  },
  SET_ALL_JOINED_GROUP(state, data) {
    state.joinedGroupList = data;
  },
  SET_GROUP_MESSAGE(state, data) {
    state.groupMessage = data;
  },
  SET_GROUP_USER_LIST(state, data) {
    state.groupUserList.push(data);
  },
  RESET_GROUP_USER(state) {
    state.groupUserList = [];
  }
};
let actions = {
  createGroup(c, i) {
    console.log(i);
    if (!i.owner || !i.ownerId || !i.groupName) return;

    let newGroup = new groupModel({
      owner: i.owner,
      ownerId: i.ownerId,
      groupName: i.groupName,
      img: i.img || ""
    });

    query.createGroup(
      db,
      newGroup,
      doc => {
        console.log(doc);
        c.commit("CREATE_GROUP_SUCCESS", { doc, groupId: newGroup.groupId });
        // c.dispatch("CREATE_GROUP_SUCCESS", newGroup);
      },
      err => console.log(err)
    );
  },
  getAllJoinedGroup(c, userId) {
    console.log(userId);
    if (userId[0] != "_") return;
    query.getAllJoinedGroup(db, userId, doc => {
      console.log(doc);
      c.commit("SET_ALL_JOINED_GROUP", doc);
    });
  },
  getGroupMessage(c, groupId) {
    if (!groupId) return;
    query.getGroupMessage(db, groupId, doc => {
      console.log(doc);
      c.commit("SET_GROUP_MESSAGE", doc);
    });
  },
  joinGroup(c, d) {
    query.joinGroup(db, d.inviteCode, d.userId, () => {
      c.dispatch("getAllJoinedGroup", d.userId);
    });
  },
  getAllGroupUser(c, userList) {
    c.commit("RESET_GROUP_USER");
    query.getAllGroupUser(db, userList, doc => {
      c.commit('SET_GROUP_USER_LIST', doc);
    })  
  },
  removeFromGroup(c, data) {
    query.removeFromGroup(db, data);
  }
};

let plugins = {};

export default {
  state,
  getters,
  mutations,
  actions,
  plugins,
};



