<template>
  <div>
    <div>
      <img id="imageShow" class="d-flex mx-auto" :src="image" style="width: 100%;max-height: 80%">
      <button
        class="close"
        @click="$emit('close')"
        style="position: absolute;right: 20px;top: 10px;"
      >
        <i class="fas fa-times-circle"></i>
      </button>
      <i class="previmg fas fa-angle-left fa-3x" @click="previmg()"></i>
      <i class="nextimg fas fa-angle-right fa-3x" @click="nextimg()"></i>
    </div>
    <div
      style="background-color: transparent;height: 112px;width: 100%;overflow: auto hidden"
      class="d-flex"
    >
      <div
        v-for="(image, i) in images"
        :key="i"
        :style="{backgroundImage: 'url('+image+')'}"
        @click="showImage(image)"
        :class="{'activeimg border-dark': active == image}"
        id="thumbnailImage"
        class="m-2"
        style="height: 96px;width: 120px;filter: blur(1.5px)"
      ></div>
    </div>
  </div>
</template>

<script>
export default {
  name: "ImageModal",
  props: {
    image: String,
    images: Array
  },
  data() {
    return {
      active: ""
    };
  },
  methods: {
    showImage(img) {
      if (document.getElementById("imageShow").src != img)
        document.getElementById("imageShow").src = img;
      this.active = img;
    },
    nextimg() {
      let i = this.images.indexOf(this.active) + 1;
      if (i < this.images.length) {
        document.getElementById("imageShow").src = this.images[i];
        this.active = this.images[i];
      } else {
        document.getElementById("imageShow").src = this.images[0];
        this.active = this.images[0];
      }
    },
    previmg() {
      let i = this.images.indexOf(this.active) - 1;
      if (i >= 0) {
        document.getElementById("imageShow").src = this.images[i];
        this.active = this.images[i];
      } else {
        document.getElementById("imageShow").src = this.images[
          this.images.length - 1
        ];
        this.active = this.images[this.images.length - 1];
      }
    }
  },
  created() {
    this.active = this.image;
  },
  mounted() {
    document.getElementsByTagName("body")[0].style.overflow = "hidden";
  },
  destroyed() {
    document.getElementsByTagName("body")[0].style.overflow = "";
  }
};
</script>

<style>
.previmg {
  left: 10px;
}
.nextimg {
  right: 10px;
}
.nextimg,
.previmg {
  position: absolute;
  top: 35%;
  cursor: pointer;
}
.nextimg:hover,
.previmg:hover {
  color: white;
}
#thumbnailImage {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  min-width: 100px;
}
.activeimg {
  border: 2px solid;
  filter: blur(0px) !important;
}
</style>



