<template>
  <div style="width: 100%">
    <hr
      class="m-2"
      v-if="next ? next.ownerId != c.ownerId || c.createdTime - next.createdTime > 300000: false"
    >
    <div class="card text-warning p-3" v-if="userInfo.blockedUser.includes(c.ownerId)">
      <code>1 blocked message</code>
    </div>

    <div v-else>
      <div>
        <div
          style="background-size: cover;background-position: center; height: 50px;width:50px;position:absolute"
          :style="'background-image: url(' + c.userImg + ');'"
          v-if="next ? next.ownerId != c.ownerId || c.createdTime - next.createdTime > 300000: true"
        ></div>
      </div>

      <div class="ml-5">
        <div
          class="text-info"
          v-if="next ? next.ownerId != c.ownerId || c.createdTime - next.createdTime > 300000: true"
        >
          {{c.owner}}
          <span class="small text-secondary">{{calendarTime(c.createdTime)}}</span>
        </div>
      </div>

      <div class="chat-wrapper">
        <div class="text" style v-emoji:message="c.message"></div>
        <img :src="img" alt="" class="img m-2 rounded" :class="{'mobile-size': isMobile}"
          v-for="(img, i) in c.img"
          :key="i"
          @click="show(img, c.img)">
        <!-- <div
          class="img m-2 rounded"
          v-for="(img, i) in c.img"
          :key="i"
          @click="show(img, c.img)"
          :style="'background-image: url(' + img + ')'"
        ></div> -->
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";

import { convertDate, isMobile } from "../utilities/index";
import ImageViewModalVue from "./ImageViewModal";

export default {
  name: "ChatMessage",
  data() {
    return {
      isShow: false,
      lastUserMessageId: "",
      lastMessageCreateTime: 0,
      isMobile: isMobile.any()
    };
  },
  computed: {
    ...mapState({
      userInfo: state => state.auth.userInfo,
      getUserStatus: state => state.auth.userInfo.status,
      getUsername: state => state.auth.userInfo.name,
      notify: state => (state.auth.notify ? state.auth.notify : []),
      inactiveNotify: state =>
        state.auth.inactiveNoti ? state.auth.inactiveNoti : [],
      pendingRequests: state => state.auth.pendingRequests
    })
  },
  props: {
    c: Object,
    next: Object
  },
  methods: {
    calendarTime: convertDate,
    lastMessage: function(ownerId) {
      this.lastUserMessageId = ownerId;
    },
    show(image, images) {
      this.$modal.show(
        ImageViewModalVue,
        { image, images },
        {
          width: this.isMobile ? "100%" : "70%",
          height: "auto",
          style: { "z-index": 9000, "border-radius": "0.125rem !important"}
        },
        {
          closed: this.hide
        }
      );
    },
    hide() {
      this.$modal.hide("view-post");
    }
  }
};
</script>


<style scoped>
#thumbnailImage {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}
.activeimg {
  border: 2px solid;
  filter: blur(0px) !important;
}
.img {
  height: auto;
  max-height: 300px;
  width: auto;
  background-repeat: no-repeat;
  background-size: cover;
  cursor: pointer;
}
.mobile-size {
  max-width: 70vw !important;
}
.chat-wrapper {
  margin-left: 60px;
  width: 100%;
}
.text {
  width: 100%;
  padding-right: 50px;
}
.text-info {
  margin-left: 10px;
  margin-bottom: 5px;
}
.user-image {
  height: 30px;
  width: 30px;
}
hr {
  width: 100%;
}
</style>



