import Vue from "vue";
import router from "vue-router";
import { store } from "../store/index";
Vue.use(router);

export default new router({
  routes: [
    {
      name: "home",
      path: "/",
      component: () => import("@/components/Homepage"),
      // beforeEnter: (to, from, next) => {
      //   let user = store.getters.userInfo.name;
      //   console.log(user);
      //   if (user) {
      //     next("/");
      //     // router.push("/");
      //   } else {
      //     next('/login');
      //   }
      // }
    },
    {
      name: "personal",
      path: "/personal",
      component: () => import("@/components/Homepage")
    },
    {
      name: "personal",
      path: "/personal/:id",
      component: () => import("@/components/Message"),
      props: true
    },
    {
      name: "groups",
      path: "/group",
      component: () => import("@/components/GroupModal"),
    },
    {
      name: "group",
      path: "/group/:id",
      component: () => import("@/components/GroupChat"),
      props: true
    },
    {
      name: "public",
      path: "/public",
      component: () => import("@/components/MainPage")
    },
    // {
    // 	name: "home",
    // 	path: "/",
    // 	component: () => import("@/components/MainPage")
    // },
    {
      name: "login",
      path: "/login",
      component: () => import("@/components/LoginScreen"),
      beforeEnter: (to, from, next) => {
        let user = store.getters.userInfo.name;
        console.log(user);
        next();
        // if (user) {
        //   next("/");
        //   // router.push("/");
        // } else {
        //   next();
        // }
      }
    },
    // {
    // 	name: "friends-post",
    // 	path: "/friends-posts/:name",
    // 	component: () => import("@/components/FriendPost"),
    // 	props: true
    // },
    // {
    // 	name: "userProfile",
    // 	path: "/user/:userId/:type",
    // 	component: () => import("@/components/UserPostView"),
    // 	props: true
    // },
    {
      name: "post",
      path: "/posts/:postId/:title",
      component: () => import("@/components/ViewPost")
    },
    {
      name: "join",
      path: "/join/:inviteCode",
      component: () => import("@/components/Join")
    },
    {
      name: "profile",
      path: "/profile",
      component: () => import("@/components/Profile"),
      beforeEnter: (to, from, next) => {
        let user = store.getters.userInfo.name;
        console.log(user);
        if (user) {
          next();
        } else {
          next("/");
        }
      }
    },
    {
      name: "userprofile",
      path: "/profile/:id",
      component: () => import("@/components/Profile"),
      props: true
    }
    // {
    //   name: 'home',
    //   path: '/',
    //   component: () => import("@/components/Login")
    // }
  ]
});



